import React from 'react';
import Styled from 'styled-components';
import WorkPic from '../../../images/work.jpg';
import Title from '../../common/Title';
import Template from './Template';

const Work = () => {
    return (
        <WorkWrapper>
            <Title title="What I Do"/>
            <div className="container">
                <div className="row box">
                    <div className="col-10 offset-1 col-md-4 offset-md-0">
                    <Template icon="fas fa-paint-brush" title="Design"/>
                    </div>
                    <div className="col-10 offset-1 col-md-4 offset-md-0">
                        <Template icon="fas fa-layer-group" title="Front-End"/>
                    </div>
                    <div className="col-10 offset-1 col-md-4 offset-md-0">
                        <Template title="Back-End" icon="fas fa-database"/>
                    </div>
                </div>
            </div>
        </WorkWrapper>
    )
}

export default Work

const WorkWrapper= Styled.div`
    width:100%;
    min-height:90vh;
    background-image:linear-gradient(rgba(0,0,0,0.7),rgba(0,0,0,0.7)),url(${WorkPic});
    background-size:100% 100%;
    background-attachment:fixed;
    
    .container{
        display:flex;
        justify-content:space-between;
    }
    .box{
        padding-top:30px;
    }
    // **********responsiveness***********
    @media screen and (min-width:768px){
        .box div:nth-child(2){
            padding-top:80px;
        }
    }
`