import React from 'react';
import Styled from 'styled-components';

const ReCard = ({title,icon,instituate,subject,gpa,details}) => {
    return (
        <ReCardWrapper>
            <div className="box">
                <h2><i class={icon}/>{title}</h2>
                <h4>{instituate}</h4>
                <p>{subject}</p>
                <p>{gpa}</p>
                <p>{details}</p>
            </div>
        </ReCardWrapper>
    )
}

export default ReCard

const ReCardWrapper= Styled.div`
    padding-top:20px;
    .box{
        color:#f1f1f6;
        padding:15px;
        background:rgba(0,0,0,0.3);
        border-radius:5px;
        transition:0.4s;
        border-radius:2px 2px 10px rgba(0,0,0,0.9);
    }
    .box:hover{
        // background:rgba(0,0,0,0.5);
    }
    .box>h2{
        font-size:1.4rem;
        color:var(--second-color);
        padding:5px 5px 8px 5px;
    }
    .box h2>i{
        font-size:25px;
        color:var(--second-color);
        margin-right:5px;
    }
    .box>h4{
        font-size:1.1rem;
        padding:0 0 0 5px;
    }
    .box>p{
        color:var(--text-color);
        padding:0px 0px 0px 5px;
        font-size:15px;
    }
`