import React from 'react'
import Styled from 'styled-components'

const Footer = () => {
    return (
        <FooterWrapper>
            <div className="container text-center">
                <p>Made by @ Sourav 2020</p>
            </div>
        </FooterWrapper>
    )
}

export default Footer

const FooterWrapper= Styled.div`
    width:100%;
    height:15vh;
    background-color:rgba(0,0,0,1);
    p{
        padding-top:40px;
        color:#f1f1f6;
    }
`