import React from 'react';
import Zoom from 'react-reveal/Zoom';
import Styled from 'styled-components';
import Mypic from '../../../images/mainimg.png';

const Intro = () => {
    return (
        <IntroWrapper>
            <Zoom>
            <h2>Let Me Introduce Myself</h2>
            <div className="box1">
                <div className="box2">
                    <div>
                    <h4><span>HI!</span> My name is Sourav dey
                </h4>
                <p>I am a MERN fullstack web developer. I can create any type of website with your requirements. My work is clean, simple, and result-driven.</p>
                </div>
                <div className="img">
                    <img src={Mypic} alt='my_photo'/>
                </div>
                </div>
            </div>
            </Zoom>
        </IntroWrapper>
    )
}

export default Intro

const IntroWrapper = Styled.div`
    width:100%;
    h2{
        padding:20px 0px 10px 0;
        border-bottom:1px solid rgba(0,0,0,0.6);
        font-size:1.6rem;
        color:var(--second-color);
    }
    .box1{
        display:flex;
        flex-direction:column;
    }
    .box1 .box2{
        display:flex;
        flex-direction:column;
        order:1;
    }
    .box2 h4{
        padding:10px 0px 5px 0px;
        font-size:1rem;
        font-weight:400;
        color:var(--text-color);
    }
    .box2 h4 span{
        font-size:2rem;
        color:rgba(75, 207, 250,1.0);
    }
    .box1 .img{
        order:-1;
    }
    .box2 img{
        width:180px;
        height:210px;
        border-radius:10px; 
    }
    .box2 p{
        font-size:15px;
        color:var(--text-color);
    }
    @media screen and (min-width:767px){
        .box1 .box2{
            flex-direction:row;
            align-items:center;
        }
        .box2 .img{
            position:relative;
            padding:10px;
            order:2;
        }
        .box2 img{
            margin-left:auto;
            width:210px;
            height:250px;
            display:block;
            border-radius:10px;
            z-index:3;
        }
        .box2 .img::before{
            content:"";
            width:90%;
            height:95%;
            position:absolute;
            border-right:2px solid rgba(15, 188, 249,1.0);
            border-bottom:2px solid rgba(15, 188, 249,1.0);
            top:25px;
            left:30px;
        }
    }
`