import React from 'react';
import Styled from 'styled-components';
import Title from '../../common/Title';
import { ProductConsumer } from '../../context/Context';
import Plan from './Plan';

export default function Package() {
    
    
    return (
        <ProductConsumer>
            {value=>{
                return(
                    <PackWrapper>
            <Title title="Our Plan"/>
            <div className="container">
                <div className="row box">
        {
            value.plan.map((item)=>{
                return(
                    <div className="col-md-4">
                        <Plan title={item.title} price={item.price} page={item.page} class1={item.class1} class2={item.class2} class3={item.class3} />
                    </div>
                )
            })
        }             
                </div>
            </div>
        </PackWrapper>
                )
            }}
        </ProductConsumer>
        
    )
}

const PackWrapper= Styled.div`
    width:100%;
    overflow:hidden;
    min-height:100vh;
    background-color:rgba(30, 39, 46,1.0);
    .box{
        padding-top:60px;
        padding-bottom:60px;
    }
    
    // **********responsiveness****************
    @media screen and (max-width:991px){
        .box .col-10 div{
            width:56%;
            height:50px;
        }
    }
    @media screen and (max-width:767px){
        .box .col-10 div{
            width:70%;
            height:40px;
        }
    }
    @media screen and (max-width:480px){
        .box .col-10 div{
            width:90%;
            height:40px;
        }
        .box input{
            font-size:18px;
        }
        .box textarea{
            font-size:16px;
        }
    }
`