import React from 'react'
import Styled from 'styled-components'

const Template = (props) => {
    return (
        <TemWrapper>
            <i className={props.icon}/>
            <h2>{props.title}</h2>
            <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Corporis deserunt ut facilis aut assumenda soluta.</p>
        </TemWrapper>
    )
}

export default Template

const TemWrapper= Styled.div`
    text-align:center;
    padding:10px;
    i{
        font-size:30px;
        color:rgba(15, 188, 249,1.0);
    }
    h2{
        text-align:center;
        color:#F8F9FA;
    }
    p{
        color:#f1f1f6;
    }
`