import React, { useState } from 'react';
import Styled from 'styled-components';
import Title from '../common/Title';
export default function Contact() {
    const [note,setNote]= useState({
        name:"",
        email:"",
        message:"",
    });
    const handleChange = (e)=>{
        const name=e.target.name;
        const val= e.target.value;
        setNote((oldvalue)=>{
            if(name=='name'){
                return {
                    name: val,
                    email:oldvalue.email,
                    message: oldvalue.message
                };
            }else if(name=='email'){
                return {
                    name: oldvalue.name,
                    email:val,
                    message: oldvalue.message
                };
            }else {
                return {
                    name: oldvalue.name,
                    email:oldvalue.email,
                    message:val
                };
            }
        });
    }
    const handleSubmit=(e)=>{
        e.preventDefault();
        setNote({
            name:"",
            email:"",
            message:""
        })
    }

    return (
        <ConWrapper id="contact">
            <Title title="Get In Touch"/>
            <div className="container">
                <div className="row box">
                    <form onSubmit={handleSubmit} autoComplete='off'>
                    <div className="col-10 offset-1 d-flex justify-content-center">
                        <div><input onChange={handleChange} value={note.name} name="name" type="text" placeholder="Name"/></div>
                    </div>
                    <div className="col-10 offset-1 d-flex justify-content-center">
                        <div><input onChange={handleChange} value={note.email} name="email" type="email" placeholder="Email"/></div>
                    </div>
                    <div className="col-10 offset-1 d-flex justify-content-center">
                        <div><textarea onChange={handleChange} value={note.message} name="message" row=" " col="" placeholder="Message"></textarea></div>
                    </div>
                    <div className="col-10 offset-1 d-flex justify-content-center">
                        <button type="submit">Submit</button>
                    </div>
                    </form>
                </div>
            </div>
        </ConWrapper>
    )
}

const ConWrapper= Styled.div`
    width:100%;
    min-height:100vh;
    background-color:rgba(30, 39, 46,1.0);
    .box{
        padding-top:60px;
        padding-bottom:60px;
    }
    .box .col-10{padding:10px;}
    .box .col-10 div{
        width:46%;
        height:50px;
        border-radius: 17px;
        background: #1E272E;
        box-shadow: inset 5px 5px 10px #0f1417, 
                    inset -5px -5px 10px #2d3a45;
        padding:5px 10px 5px 15px;
    }
    .box .col-10:nth-child(3) div{
        height:200px;
    }
    .box input,
    .box textarea{
       outline:none;
       border:none;
       background-color:transparent;
    }
    .box input{
        width:100%;
        height:100%;
        font-size:22px;
        color:rgba(15, 188, 249,0.9);
        caret-color:rgba(15, 188, 249,1.0);
    }
    .box textarea{
        width:100%;
        height:100%;
        font-size:20px;
        resize:none;
        color:rgba(15, 188, 249,0.9);
        caret-color:rgba(15, 188, 249,1.0);
    }
    button{
        width:180px;
        height:40px;
        border-radius:20px;
        padding:5px 10px;
        font-size:18px;
        border:1px solid rgba(15, 188, 249,1.0);
        background-color:transparent;
        color:#F8F9FA;
        outline:none;
    }
    // **********responsiveness****************
    @media screen and (max-width:991px){
        .box .col-10 div{
            width:56%;
            height:50px;
        }
    }
    @media screen and (max-width:767px){
        .box .col-10 div{
            width:70%;
            height:40px;
        }
    }
    @media screen and (max-width:480px){
        .box .col-10 div{
            width:90%;
            height:40px;
        }
        .box input{
            font-size:18px;
        }
        .box textarea{
            font-size:16px;
        }
    }
`