import React from 'react';
import Zoom from 'react-reveal/Zoom';
import Styled from 'styled-components';

const Contact = () => {
    return (
        <ConWrapper>
            <Zoom>
            <h4><span>For hire me</span> Please Contact with me</h4>
            <div className="box">
                <div className="box1">
                    <p>
                        <i className="fas fa-home"/>Address
                    </p>
                    <p>
                        Dhaka,Bangladesh
                    </p>
                </div>
                <div className="box2">
                    <p>
                        <i className="far fa-envelope"/>Email
                    </p>
                    <p>
                        deysourav654@gmail.com
                    </p>
                </div>
                <div className="box3">
                    <p>
                        <i className="fas fa-phone"/>Phone
                    </p>
                    <p>
                        01680510162
                    </p>
                </div>
            </div>
            <div className="social">
                <a href="https://www.facebook.com/souravsd0" target="_blank" title="Facebook" rel="noreferrer"><i className="fab fa-facebook-f"/></a>
                <a href="https://twitter.com/souravd70242726" target="_blank" title="Google+" rel="noreferrer"><i className="fab fa-twitter"/></a>
                <a href="https://www.instagram.com/souravsd02/" target="_blank" title="Skype" rel="noreferrer"><i className="fab fa-instagram"></i></a>
                <a href="https://www.youtube.com/channel/UCdYvAi7AULPw2Q78wmNGKqw?view_as=subscriber" target="_blank" title="Youtube" rel="noreferrer"><i className="fab fa-youtube"/></a>
                <a href="#" target="_blank" title="Linkdin"><i className="fab fa-linkedin-in"/></a>
            </div>
            </Zoom>
        </ConWrapper>
    )
}

export default Contact

const ConWrapper= Styled.div`
    width:100%;
    h4{
        padding:10px 0px 5px 0px;
        font-size:1.2rem;
        font-weight:400;
        border-bottom:1px solid rgba(0,0,0,0.6);
    }
    h4 span{
        font-size:2rem;
        color:rgba(75, 207, 250,1.0);
        padding-right:5px;
    }
    .box{
        width:100%;
        padding:20px 0px;
        display:flex;
        flex-direction:column;
        justify-content:space-between;
    }
    .box1,
    .box2,
    .box3{
        display:flex;
        justify-content:space-between;
    }
    .box1 p,
    .box2 p,
    .box3 p{
        color:var(--text-color);
    }
    .box .box1 i,
    .box .box2 i,
    .box .box3 i{
        color:rgba(15, 188, 249,1.0);
        padding-right:10px;
        transform:scale(1);
    }
    .social{
        display:flex;
    }
    .social a{
        display:block;
        text-decoration:none;
        padding:5px 5% 0 5%;
        color:#F8F9FA;
        cursor:pointer;
    }
    .fab{
        transition:0.5s;
        font-size:25px;
    }
    .fa-facebook-f:hover{
        color:#3b5999;
        transform:scale(2);
    }
    .fa-twitter:hover{
        color:#1DA1F2;
        transform:scale(2);
    }
    .fa-instagram:hover{
        color:#CA006E;
        transform:scale(2);
    }
    .fa-youtube:hover{
        color:#cd201f;
        transform:scale(2);
    }
    .fa-linkedin-in:hover{
        color:#0084ff;
        transform:scale(2);
    }
    // **********responsiveness**********
    @media screen and (min-width:770px){
        .box{
            width:90%;
        }
    }
    @media screen and (max-width:375px){
        .box1,
        .box2,
        .box3{
            flex-direction:column;
            justify-content:center;
            align-items:center;
        }
    }
`