import React from 'react';
import Bounce from 'react-reveal/Bounce';
import Styled from 'styled-components';
import Title from '../../common/Title';
import { ProductConsumer } from "../../context/Context";
import Card from './Card';

const Services = () => {
return (
    <ProductConsumer>
        {value=>{
            return(
    <ServiceWrapper id="services">
        <Title title="Our Serviecs"/>
        <div className="container">
            <div className="row">
                <Bounce left duration="3000">
    {
        value.services.map((item,index)=>{
                    return(
                        <div className="col-12 col-md-6 col-lg-4"><Card name={item.name} icon={item.class} details={item.details}/></div>
                    )
        })
    }
                </Bounce>
                <Bounce right duration='3000'>
    {/* {
        value.services.map((item,index)=>{
            if(index>3){
                return(
                    <div className="col-12 col-md-6 col-lg-4"><Card name={item.name} icon={item.class} details={item.details}/></div>
                )
            }
        })
    } */}
                </Bounce>
            </div>
        </div>
    </ServiceWrapper>)
}}
</ProductConsumer>
) 
}

export default Services

const ServiceWrapper= Styled.div`
    width:100%;
    min-height:100vh;
    background-color:rgba(30, 39, 46,1.0);
    .container{
        padding:0px 0px 60px 0;
    }
`