import React from 'react';
import Roll from 'react-reveal/Roll';
import Zoom from 'react-reveal/Zoom';
import Styled from 'styled-components';


const Title = (props) => {
    return (
        <TitleWrapper>
        <div className='container'>
            <Roll>
                <h2>{props.title}</h2>
            </Roll>
            <Zoom duration='2000' left cascade>
                <div className="roll"></div>
            </Zoom>
        </div>
        </TitleWrapper>
    )
}

export default Title

const TitleWrapper= Styled.div `
    .container{
        display:flex;
        flex-direction:column;
        align-items:center;
    }
    h2{
        padding-top:110px;
        font-size:2rem;
        color:#F8F9FA;
        text-align:center;
    }
    .roll{
        width:150px;
        height:1px;
        background-color:rgba(15, 188, 249,1);;
    }
`