import React, { useState } from 'react';
import Styled from 'styled-components';
import Img1 from '../../../images/img1.PNG';
import Img2 from '../../../images/img2.PNG';
import Img3 from '../../../images/img3.PNG';
import Img4 from '../../../images/img4.PNG';
import Img5 from '../../../images/img5.PNG';
import Img6 from '../../../images/img6.jpg';
import Title from '../../common/Title';
import { ProductConsumer } from '../../context/Context';

const Images=[
    {
        id:1,
        img:Img1,
        title:'Design',
        topic:"Frontend",
        name:"My Portfolio",
        date:'20 Dec 2019'
    },
    {
        id:2,
        img:Img2,
        title:'Website',
        topic:"Frontend",
        name:"Arsha template clone",
        date:'1 Jan 2020'
    },
    {
        id:3,
        img:Img3,
        title:"design",
        topic:"Fullstack",
        name:'Launchweb Company',
        date:'4 Jan 2021'
    },
    {
        id:4,
        img:Img4,
        title:'Website',
        topic:"Fullstack",
        name:"Demu Website",
        date:'20 Dec 2019'
    },
    {
        id:5,
        img:Img5,
        title:'Design',
        topic:"Fullstack",
        name:"Messaging App",
        date:'03 Feb 2021'
    },
    {
        id:6,
        img:Img6,
        title:'Website',
        topic:"Frontend",
        date:'24 May 2020'
    }
]
const Portfolio = () => {
    const [link,setLink]= useState();
    const [val,setVal]= useState(false);
    const [portfolio,setPort]= useState(Images);
    const [cat,setCat]= useState(0);

    const handleClick=(img)=>{
        setLink(img);
        setVal(true);
    }
    const handleDel=()=>{
        setVal(false);
    }
    const handleCat=(id)=>{
        const img= Images;
        console.log(id);
        if(id==0){
            setPort(img);
            setCat(id);
        }else if(id==1){
            setPort(()=>{
                return img.filter((val)=>{
                    return val.topic != "Fullstack";
                })
            })
            setCat(id);
        }else{
            setPort(()=>{
                return img.filter((val)=>{
                    return val.topic !="Frontend";
                })
            })
            setCat(id);
        }
    }

    return (
        <ProductConsumer>
            {value=>{
                return(
                    <ProWrapper id="portfolio" show={val} cate={cat}>
            <Title title="Some Work"/>
            <div className="container">
                <div className="row box1">
                    <div className="col-12 offset-0 category ">
                        {
                            value.portfolio.map((val,idn)=>{
                                return(<p className="active" key={val.id} onClick={()=>handleCat(idn)}>{val.name}</p>)
                            })
                        }
                    </div>
                </div>
                <div className="row box2 text-center">
            {
                portfolio.map((val,ind)=>{
                    return(
                        <div key={ind} id={ind} className='main-box col-12 offset-1 col-sm-6 offset-sm-0 col-md-4'>
                            <img src={val.img} alt={val.title} width="100%" height="100%"/>
                            <div className="txt">
                                <h4>{val.topic}</h4>
                                <p>{val.date}</p>
                                <button onClick={()=>handleClick(val.img)}>Preview</button>
                            </div>
                        </div>
                    )
                })
            }
                </div>
            </div>
            <div className="popup">
                <div className="singlepop">
                    <img src={link} alt="design" width='100%' height="100%" />
                    <i onClick={handleDel} className='fas fa-times-circle'/>
                </div>
            </div>
        </ProWrapper>
                )
            }}
        </ProductConsumer>
    )
}

export default Portfolio

const ProWrapper= Styled.div`
    width:100%;
    min-height:100vh;
    background-color:rgba(30, 39, 46,1.0);
    .container{
        padding:5px 0 30px 0;
    }
    .box1{padding-top:10px;}
    .box1 p{
        display:inline-block;
        font-size:20px;
        color:#F8F9FA;
        padding:5px 10px;
        cursor:pointer;
    }
    .box2{
        display:flex;
        justify-content:center;
        padding-bottom:30px;
    }
    .box2 .main-box{
        margin:5px;
        width:360px;
        height:250px;
        position:relative;
        padding:5px;
        box-sizing:border-box;
        overflow:hidden;
        transition:0.5s;
    }
    .box2 .main-box:hover .txt{
        left:0;
        opacity:0.8;
    }
    .txt button:hover{
        background-color:rgba(15, 188, 249,1.0);
        color:#F8F9FA;
        border:none;
    }
    .main-box img{
        width:100%;
        height:100%;
        position:absolute;
        z-index:1;
        object:fit;
        top:0;
        left:0;
    }
    .box2 .txt{
        width:100%;
        height:100%;
        position:absolute;
        z-index:2;
        top:0;
        left:-100%;
        display:flex;
        justify-content:center;
        align-items:center;
        flex-direction:column;
        transition:0.7s;
        transition-delay:0.2s;
        background-color:black;
    }
    .txt h4{
        color:#F8F9FA;
        font-size:30px;
    }
    .txt p{
        font-size:20px;
        color:#f1f1f6;
    }
    .txt button{
        width:120px;
        padding:5px 10px;
        font-size:18px;
        outline:none;
        background-color:transparent;
        border:1px solid rgba(15, 188, 249,1.0);
        border-radius:30px;
        color:rgba(15, 188, 249,1.0);
        transition:0.5s;
    }
    .popup{
        width:100%;
        height:100vh;
        position:fixed;
        top:0;
        left:0;
        right:0;
        bottom:0;
        background-color:rgba(0,0,0,0.8);
        z-index:20;
        display:${props=>props.show? 'block':'none'};
    }
    .popup .singlepop{
        width:70%;
        height:80vh;
        margin:5% auto;
        background:#fff;
        position:relative;
    }
    .singlepop i{
        font-size:30px;
        color:#fff;
        position:absolute;
        z-index:1;
        top:10px;
        left:95%;
        transition:0.4s;
        color:var(--second-color);
    }
    .singlepop i:hover{
        color:red;
    }
    .category p:nth-child(${props=>props.cate+1}){
        color:rgba(15, 188, 249,1.0);
        border-bottom:1px solid rgba(15, 188, 249,1.0);
    }
    // **********responsive***********
    @media screen and (max-width:427px){
        .box2 .main-box{
            width:330px;
            height:250px;
        }
        .popup .singlepop{
            width:100%;
            height:80vh;
        }
        .singlepop img{
            width:100%;
            height:100%;
        }
    }
    @media screen and (max-width:767){
        .popup .singlepop{
            width:100%;
            height:80vh;
        }
        .singlepop img{
            width:100%;
            height:100%;
        }
    }
`