import React, { useState } from 'react';
import Fade from 'react-reveal/Bounce';
import Styled from 'styled-components';
import Title from '../../common/Title';
import { ProductConsumer } from '../../context/Context';
import Contact from './Contact';
import Hobbies from './Hobbies';
import Intro from './Intro';

const About = ()=>{
    const [val,setVal]= useState(1);
    const handleClick=(id)=>{
        setVal(id);
    }
    return(
        <ProductConsumer>
            {value=>{
                return (
                    <AboutWrapper id='about' act={val} >
            <div className="container">
                <Title title="About Me"/>
                <div className="row">
                        <Fade left cascade>
                            <div className="category col-10 col-md-2 offset-1 offset-md-1 offset-lg-1">
                                {
                                    value.about.map((val)=>{
        return (
            <div className="box" key={val.id} >
                <i className={val.icon}/>
                <h3 onClick={()=>handleClick(val.id)}>{val.name}</h3>    
            </div>
        )
                                    })
                                }
                            </div>
                        </Fade>
                    <div className="discription col-10 col-lg-6 col-md-8  col-sm-10 offset-lg-0 offset-1 ">
                        {(()=>{
                            if(val==1){
                                return <Intro/>
                            }else if(val==2){
                                return <Hobbies/>
                            }else{
                                return <Contact/>
                            }
                        })()}
                    </div>
                </div>
            </div>
            
        </AboutWrapper>
                )
            }}
        </ProductConsumer>
        
    );
}

export default About;

const AboutWrapper= Styled.div `
    width:100%;
    height:120vh;
    background-color:rgba(30, 39, 46,1.0);
    .category{
        padding:60px 10px;
        display:flex;
        flex-direction:column;
    }
    .box{
        position:relative;
        padding-bottom:10px;
    }
    .box>h3{
        cursor:pointer;
    }
    .category .box:nth-child(${props=>props.act}) h3{
        color:rgba(75, 207, 250,1.0);
    }
    .category .fas{
        color:rgba(15, 188, 249,1.0);
        position:absolute;
        font-size:1.2rem;
        transform:rotate(0deg);
    }
    .category h3{
        padding-left:15px;
        color:#F8F9FA;
        font-size:1.2rem;
        font-weight:400;
    }
    .discription{
        padding-top:60px;
        color:#fff;
    }
    @media screen and (max-width:767px){
        .category{
            display:flex;
            flex-direction:row;
            padding:60px 10px 10px 10px;
        }
        .category h3{
            font-size:1rem;
            padding:0px 10px 0 20px;
        }
        .category .fas{
            top:0px;
            left:3px;
            transform:rotate(90deg);
        }
        .discription{
            padding-top:10px;
            color:#fff;
        }
    }
`