import React from 'react';
import Styled from 'styled-components';

const Plan = ({title,price,page,class1,class2,class3}) => {
    return (
        <PlanWrapper>
            <h2>{title}</h2>
            <h1><sup>$</sup>{price}</h1>
            <p>{page}</p>
            <p><a href="#">ORDER NOW</a></p>
            <ul>
                <li><i class="fas fa-check"/>1 Page</li>
                <li><i class="fas fa-check"/>Slider/Scroller</li>
                <li><i class="fas fa-check"/>Browser Compatibility</li>
                <li><i class={class1}/>Responsive Design</li>
                <li><i class={class2}/>Server Upload</li>
                <li><i class={class3}/>24/7 Support</li>
            </ul>
        </PlanWrapper>
    )
}

export default Plan;

const PlanWrapper = Styled.div`
    width:90%;
    border:1px solid rgba(255,255,255,0.4);
    color:#fff;
    padding:10px 0px 10px 0px;
    border-radius:10px;
    transition:0.5s;
    >h2{
        font-size:20px;
        text-align:center;
        padding:5px;
        font-weight:400;
        color:var(--text-color);
    }
    >h1{
        font-size:3.5rem;
        text-align:center;
        padding:5px;
        font-weight:600;
        color:var(--second-color);
    }
    >h1>sup{
        font-size:1.5rem;
        color:var(--text-color);
    }
    >p{
        font-size:15px;
        color:var(--text-color);
        text-align:center;
    }
    >p>a{
        display:inline-block;
        width:100%;
        padding:10px 15px;
        background:var(--second-color);
        color:var(--main-color);
        font-size:20px;
        font-weight:500;
        transition:0.5s;
    }
    >p>a:hover{
        color:white;
    }
    >ul{
        border-top:1px solid rgba(255,255,255,0.1);
        padding:10px 30px;
    }
    >ul>li{
        padding:4px;
        font-size:16px;
        color:text-color;
        color:var(--text-color);
    }
    >ul>li>i{
        font-size:15px;
        padding-right:6px;
    }
    .fa-check{
        color:var(--second-color);
    }
    .fa-times{
        color:rgba(255,0,0,0.7);
    }
    :hover{
        transform:scale(1.1);
    }
`;