import React, { useState } from 'react';
import { Link } from 'react-scroll';
import Styled from 'styled-components';
import { ProductConsumer } from '../context/Context';

export default function Navbar() {
    const [val,setVal]= useState(1);
    const [scrolly,setScrolly]= useState(0);
    
    const handleClick=(id)=>{
        setVal(id);
    }
    window.addEventListener("scroll",()=>{
        console.log(window.scrollY);
        setScrolly(window.scrollY);
        const y=window.scrollY;
        if(y<500){
            setVal(1);
        }else if(y>500 && y<1600){
            setVal(2);
        }else if(y>1600 && y<2700){
            setVal(3);
        }else if(y>2700 && y<7280){
            setVal(4);
        }else if(y>7280){
            setVal(5);
        }else{

        }
    });

    return (
        <ProductConsumer>
            {value=>{
                return(<NavWrapper scr={scrolly} active={val} show={value.sidebarOpen} bar={value.barClick}>
                <div className="header-container">
                    <nav>
                        <div className="logobtn">
                            <Link to="home">SD</Link>
                            <div className="btn" onClick={value.handleSidebar}>
                                <div className="bar"></div>
                                <div className="bar"></div>
                                <div className="bar"></div>
                            </div>
                        </div>
                        <ul className="links" onClick={value.handleSidebar}>
                            {

        value.links.map((link)=>{
            return(
                <li key={link.id}>
                    <Link onClick={()=>{handleClick(link.id)}}
      to={link.path}
      spy={true}
      smooth={true}
      hashSpy={true}
      offset={0}
      duration={1000}
      delay={500}
      isDynamic={true}
      ignoreCancelEvents={false}
>
  {link.name}
</Link></li>
            )
        })
                            }
                        </ul>
                    </nav>
                </div>
            </NavWrapper>)
            }}
        </ProductConsumer>
    )
}

const NavWrapper= Styled.div`
    position:fixed;
    width:100%;
    top:0;
    z-index:6;
    background:${props=>props.scr>440? "rgba(0,0,0,1)":"rgba(0,0,0,0.3)"};
    transition:all 0.5s ease-in-out;
    opacity:0.9;
    
    .links li:nth-child(${props=>props.active}) a{
        color:rgba(15, 188, 249,1.0);
    }
    .header-container{
        width:100%;
        z-index:5;
    }
    .logobtn a{
        display:block;
        padding:0px 0px 0px 10px !important;
        font-size:2.5rem;
        color:#fff;
        font-weight:800;
        cursor:pointer;
    }
    .logobtn a:hover{
        color:#fff;
    }
    .logobtn{
        width:100%;
        display:flex;
    }
    .btn{
        margin-left:auto;
    }
    .bar{
        width:35px;
        margin:5px;
        padding:2px;
        background:rgba(15, 188, 249,1.0);
    }
    .logobtn{
        display:flex;
        justify-content:space-between;
        align-items:center;
        border-bottom:1px solid gray;
        padding:10px;
    }
    .links{
        transition:all 0.5s ease-in-out;
        position:fixed;
        top:71px;
        left:0px;
        bottom:0px;
        right:0px;
        background:rgba(0,0,0,0.9);
        width:400px;
        transform:${props=>props.show ? 'translateX(0%)': 'translateX(-100%)'} ;
    }
    .links li a{
        color:#f1f1f6;
        display:block;
        text-transform:uppercase;
        padding:10px;
        transition:all 0.5s ease-in-out;
        border:1px solid transparent;
        border-bottom:1px solid transparent;
        cursor:pointer;
        font-size:16px;
        font-family: 'Roboto Slab', serif;
        letter-spacing:1.3px;
    }
    .links li a:hover{
        color:rgba(15, 188, 249,1.0);
        padding:10px 25px;
        border-bottom:1px solid rgba(75, 207, 250,1.0);
        background:rgba(0,0,0,0.1);
    }
    //responsiveness
    @media screen and (min-width:870px){
        .links{
            transition:all 0s ease-in-out !important;
        }
    }
    @media screen and (min-width:880px){
        .header-container{
            margin:0 auto;
            width:100%;
        }
        .btn{
            display:none;
        }
        nav{
            display:flex;
            justify-content:space-between;
            padding:10px 0;
            max-width:1180px;
            margin:0 auto;
            position:relative;
        }
        .links{
            position:relative;
            transition:all 0s ease-in-out !important;
            display:flex;
            margin-right:10px;
            justify-content:space-between;
            align-items:center;
            top:auto;
            background:transparent;
            width:auto;
            transform:translate(0);
        }
        .links li a{
            margin:0 2px;
        }
        .links li a:hover{
            margin:0 2px;
            padding:10px;
            border-bottom:1px solid rgba(75, 207, 250,1.0);
            background:transparent;
            color:rgba(15, 188, 249,1.0);
        }
        .logobtn{
            border-bottom:0;
        }
        @media screen and (min-width:960px){
            .logobtn a{
                display:block;
                padding:0px;
            }
        }
    }
`