import React from 'react';
import Styled from 'styled-components';
import Title from '../../common/Title';
import { ProductConsumer } from '../../context/Context';
import ReCard from './ReCard';


export default function Resume() {
    return (
        <ProductConsumer>
        {value=>{
            return(
                <ResumeWrapper>
            <Title title="My Resume"/>
            <div className="container">
                <div className="row">
                    <div className="col-md-6 box1">
                        <h2>Education</h2>
{
    value.education.map((item)=>{
        return(
            <ReCard title={item.title} instituate={item.instituate} subject={item.subject} gpa={item.gpa} details={item.details} icon={item.icon} />
        )
    })
}
                        
                    </div>
                    <div className="col-md-6 box2">
                        <h2>Experience</h2>
                        {
    value.experience.map((item)=>{
        return(
            <ReCard title={item.title} instituate={item.instituate} subject={item.subject} gpa={item.gpa} details={item.details} icon={item.icon} />
        )
    })
}
                    </div>
                </div>
            </div>
            
        </ResumeWrapper>
            )
        }}
        </ProductConsumer>
    )
}

const ResumeWrapper= Styled.div`
    width:100%;
    min-height:100vh;
    background-color:rgba(30, 39, 46,1.0);
    .row{
        padding-top:60px;
        padding-bottom:60px;
        color:#fff;
    }
    .row>.box1,
    .row>.box2{
        padding:5px 20px;
    }
    // **********responsiveness****************
    @media screen and (max-width:991px){
        .box .col-10 div{
            width:56%;
            height:50px;
        }
    }
    @media screen and (max-width:767px){
        .box .col-10 div{
            width:70%;
            height:40px;
        }
    }
    @media screen and (max-width:480px){
        .box .col-10 div{
            width:90%;
            height:40px;
        }
        .box input{
            font-size:18px;
        }
        .box textarea{
            font-size:16px;
        }
    }
`