import React, { useState } from 'react';
import CountUp from 'react-countup';
import Styled from 'styled-components';
import WorkPic from '../../images/journey.jpg';
import Title from '../common/Title';


const Journey = () => {
    const [value,setValue] = useState({
        completed:0,
        clients:0,
        experience:0,
        awards:0
    });
    const scrollTop = ()=>{
        if(window.scrollY>=6658 && window.scrollY<7258){
            setValue({completed:15,clients:15,experience:3,awards:5});
        }
    }
    window.addEventListener('scroll',scrollTop)

    if(window.scrollY>=7158){
        window.removeEventListener('scroll',scrollTop);
    }
    return (
        <JourneyWrapper>
            <Title title="Life's Journey"/>
            <div className="container">
                <div className="row box">
                    <div className="col-md-3 col-sm-6 total">
                        <i className="far fa-laugh-beam"/>
                        <h2>Happy Clients</h2>
                        <CountUp start={0} end={value.clients} delay={0} duration={2}>
                            {({ countUpRef }) => (
                                <div>
                                <span class="counter" ref={countUpRef} />
                                </div>
                            )}
                        </CountUp>
                    </div>  
                    <div className="col-md-3 col-sm-6 total">
                        <i className="far fa-handshake"/>
                        <h2>Project Completed</h2>
                        <CountUp start={0} end={value.completed} delay={0} duration={2}>
                            {({ countUpRef }) => (
                                <div>
                                <span class="counter" ref={countUpRef} />
                                </div>
                            )}
                        </CountUp>
                    </div>  
                    <div className="col-md-3 col-sm-6 total">
                        <i className="fas fa-user-tie"/>
                        <h2>Year of experience</h2>
                        <CountUp start={0} end={value.experience} delay={0} duration={2}>
                            {({ countUpRef }) => (
                                <div>
                                <span class="counter" ref={countUpRef} />
                                </div>
                            )}
                        </CountUp>
                    </div>  
                    <div className="col-md-3 col-sm-6 total">
                        <i className="fas fa-award"/>
                        <h2>Awards</h2>
                        <CountUp start={0} end={value.awards} delay={0} duration={2}>
                            {({ countUpRef }) => (
                                <div>
                                <span class="counter" ref={countUpRef} />
                                </div>
                            )}
                        </CountUp>
                    </div>  
                </div>
            </div>
        </JourneyWrapper>
    )
}

export default Journey

const JourneyWrapper= Styled.div`
    width:100%;
    min-height:60vh;
    background-image:linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.5)),url(${WorkPic});
    background-size:100% 100%;
    background-attachment:fixed;
    
   .box{
       padding-top:60px;
       color:var(--text-color);
       padding-bottom:90px;
   }
   .box .total{
       text-align:center;
   }
   .box .total i{
       font-size:40px;
   }
   .box .total h2{
       font-size:20px;
   }
   .box .total .counter{
       font-size:30px;
       color:var(--second-color);
   }
`