import React from 'react';
import Styled from 'styled-components';

const Quate = () => {
    return (
        <QuateWrapper>
            <div className="container">
                <blockquote class="blockquote text-center">
                    <p class="mb-0">Any fool can write code that a computer can understand. Good programmers write code that humans can understand.</p>

                    <footer class="blockquote-footer"><cite title="Source Title"> Martin Fowler</cite></footer>
                </blockquote>
            </div>
        </QuateWrapper>
    )
}

export default Quate

const QuateWrapper= Styled.div`
    width:100%;
    min-height:30vh;
    background-color:#2f3640;
    overflow:hidden;
    p{
        padding:30px 10px 20px 10px;
        color:rgba(15, 188, 249,1.0);
        font-size:25px;
    }
    .blockquote-footer{
        font-size:18px;
        color:#F8F9FA;
    }
`