import React from 'react';
import Styled from 'styled-components';

const Card = ({name,icon,details}) => {
    return (
        <CardWrapper>
            <div className="box">
                <p><i className={icon}/></p>
                <h3>{name}</h3>
                <p>{details}</p>
            </div>
        </CardWrapper>
    )
}

export default Card

const CardWrapper= Styled.div`
    padding-top:20px;
    .box{
        color:#f1f1f6;
        padding:15px;
        border-radius: 17px;
        background: #1E272E;
        box-shadow:  5px 5px 10px #171e23, 
                     -5px -5px 10px #253039;
        transition:0.5s;
    }
    .box:hover{
        border-radius: 17px;
        background: #1E272E;
        box-shadow: inset 5px 5px 10px #0f1417, 
                    inset -5px -5px 10px #2d3a45;
    }
    .box p>i{
        font-size:32px;
        color:rgba(255,255,255,0.5);
    }
    .box h3{
        text-align:center;
        color:#F8F9FA;
        font-size:20px;
        color:var(--second-color);
    }
    .box p{
        font-size:15px;
        text-align:center;
        color:var(--text-color);
    }
`