import React, { useState } from 'react';
import Styled from 'styled-components';
import Img from '../../images/skill.png';
import Title from '../common/Title';
const Skill = () => {
    const [val,setVal]= useState(false);

    window.addEventListener("scroll",()=>{
        if(window.scrollY>3552){
            setVal(true);
        }
    });
    return (
        <SkillWrapper skill={val}>
            <Title title="My Skill"/>
            <div className="container d-flex">
                <div className="front">
                    <div className="show1">
                        <span>F</span>
                        <span>R</span>
                        <span>O</span>
                        <span>N</span>
                        <span>T</span>
                        <span>E</span>
                        <span>N</span>
                        <span>D</span>
                    </div>
                    <div className="css">
                        <p>CSS3</p>
                        <div  className="box"><span>95%</span></div>
                    </div>
                    <div className="js">
                        <p>Javascript</p>
                        <div  className="box"><span>90%</span></div>
                    </div>
                    <div className="react">
                        <p>Reactjs</p>
                        <div  className="box"><span>90%</span></div>
                    </div>
                    <div className="bootstrap">
                        <p>Nextjs</p>
                        <div  className="box"><span>80%</span></div>
                    </div>
                </div>
                <div className="back">
                    <div className="node">
                        <p>Nodejs</p>
                        <div  className="box"><span>90%</span></div>
                    </div>
                    <div className="express">
                        <p>Express</p>
                        <div  className="box"><span>80%</span></div>
                    </div>
                    <div className="mdb">
                        <p>Mongodb</p>
                        <div  className="box"><span>90%</span></div>
                    </div>
                    <div className="php">
                        <p>Wordpress</p>
                        <div  className="box"><span>90%</span></div>
                    </div>
                    <div className="show2">
                        <span>B</span>
                        <span>A</span>
                        <span>C</span>
                        <span>K</span>
                        <span>E</span>
                        <span>N</span>
                        <span>D</span>
                    </div>
                </div>
            </div>
        </SkillWrapper>
    )
}

export default Skill

const SkillWrapper= Styled.div`
    width:100%;
    min-height:90vh;
    background-image:linear-gradient(rgba(0,0,0,0.7),rgba(0,0,0,0.9)),url(${Img});
    background-size:100% 100%;
    background-attachment:fixed;
    .container{
        padding:00px 20px;
        color:#f1f1f6;
        padding-left:8%;
    }
    .front,
    .back{
        width:50%;
        padding-top:40px;
        position:relative;
        top:0;
    }
    .front div,
    .back div{
        margin-bottom:20px;
    }
    p{
        font-size:20px;
        color:#F8F9FA;
    }
    .box{
        width:80%;
        height:14px;
        padding:2px 0px;
        background-color:rgba(255,255,255,0.1);
        border-radius:30px;
        position:relative;
        color:rgba(15, 188, 249,1.0);
        text-align:center;
        z-index:2;
    }
    .show1{
        position:absolute;
        z-index:3;
        left:-20%;
    }
    .show2{
        position:absolute;
        right:0;
        z-index:4;
        top:70px;
    }
    .show1 span,
    .show2 span{
        display:block;
        font-size:20px;
        padding-bottom:10px;
    }
    .box span{
        position:absolute;
        z-index:5;
        color:rgba(15, 188, 249,1.0);
        font-size:15px;
        top:-25px;
    }
    .box::before{
        content:"";
        width:0%;
        height:14px;
        border-radius:30px;
        position:absolute;
        top:0;
        left:0;
    }
    .css .box::before{
        background-color:#FF5733;
        animation:${props=>props.skill? 'css 2s 0.3s ease-in-out 1' : 'none'};
        animation-fill-mode:forwards;
    }
    .bootstrap .box::before{
        background-color:#673AB7;
        animation:${props=>props.skill? 'bootstrap 2s 0.3s ease-in-out 1' : 'none'};
        animation-fill-mode:forwards;
    }
    .express .box::before{
        background-color:#FF9326;
        animation:${props=>props.skill? 'bootstrap 2s 0.3s ease-in-out 1' : 'none'};
        animation-fill-mode:forwards;
    }
    .js .box::before{
        background-color:#F7DF1E;
        animation:${props=>props.skill? 'js 2s 0.3s ease-in-out 1' : 'none'};
        animation-fill-mode:forwards;
    }
    .react .box::before{
        background-color:rgba(15, 188, 249,1.0);
        animation:${props=>props.skill? 'js 2s 0.3s ease-in-out 1' : 'none'};
        animation-fill-mode:forwards;
    }
    .node .box::before{
        background-color:#8BC500;
        animation:${props=>props.skill? 'js 2s 0.3s ease-in-out 1' : 'none'};
        animation-fill-mode:forwards;
    }
    .mdb .box::before{
        background-color:#10AA50;
        animation:${props=>props.skill? 'mongo 2s 0.3s ease-in-out 1' : 'none'};
        animation-fill-mode:forwards;
    }
    .php .box::before{
        background-color:#207196;
        animation:${props=>props.skill? 'mongo 2s 0.3s ease-in-out 1' : 'none'};
        animation-fill-mode:forwards;
    }
    @keyframes css{
        0%{
            width:0%;
        }
        100%{
            width:95%;
        }
    }
    @keyframes js{
        0%{
            width:0%;
        }
        100%{
            width:90%;
        }
    }
    @keyframes bootstrap{
        0%{
            width:0%;
        }
        100%{
            width:80%;
        }
    }
    @keyframes mongo{
        0%{
            width:0%;
        }
        100%{
            width:90%;
        }
    }
    // ************responsiveness************
    @media screen and (max-width:560px){
        p{
            font-size:16px;
        }
        .show1 span,
    .show2 span{
        font-size:15px;
        padding-bottom:5px;
    }
    .show1{
        left:-15%;
        top:70px;
    }
    }
    @media screen and (max-width:420px){
        .show1,
        .show2{
            display:none;
        }
    }
`