import React from 'react';
import Particles from 'react-particles-js';
import Bounce from 'react-reveal/Bounce';
import { Link } from 'react-scroll';
import Typed from 'react-typed';
import Styled from 'styled-components';
import BackgroundImage from '../../images/background.jpg';
import ProfilePic from '../../images/profile.png';
import Navbar from '../common/Navbar';

const Home = () => {
    return (
        <HomeWrapper id="home">
            <Particles
            canvasClassName="particle"
            params={{
                "particles": {
                    "number": {
                        "value": 160,
                        "density": {
                            "enable": false
                        }
                    },
                    "size": {
                        "value": 3,
                        "random": true,
                        "anim": {
                            "speed": 4,
                            "size_min": 0.3
                        }
                    },
                    "line_linked": {
                        "enable": false
                    },
                    "move": {
                        "random": true,
                        "speed": 1,
                        "direction": "top",
                        "out_mode": "out"
                    }
                },
                "interactivity": {
                    "events": {
                        "onhover": {
                            "enable": true,
                            "mode": "bubble"
                        },
                        "onclick": {
                            "enable": true,
                            "mode": "repulse"
                        }
                    },
                    "modes": {
                        "bubble": {
                            "distance": 250,
                            "duration": 2,
                            "size": 0,
                            "opacity": 0
                        },
                        "repulse": {
                            "distance": 400,
                            "duration": 4
                        }
                    }
                }
            }} />
            
            <Navbar/>
            <div className="container">
                <div className="row pt-4 pb-3 box1">
                    <div className="col-12">
                        <h1 className='brand text-light '>SOURAV DEY</h1>
                    </div>
                    <div className="col-12 row">
                        <div className="col-12 col-md-5 col-lg-4 d-flex ">
                            <Bounce top duration='3000'>
                                <Link className='btn-1' to='home'>Hire me</Link>
                            </Bounce>
                            <Bounce bottom duration='3000'>
                                <Link className='btn-2' to='portfolio'> Get CV</Link>
                            </Bounce>
                        </div>
                    </div>
                </div>
                <div className="row pt-3 pt-md-4 justify-content-center box2">
                        <div className="col-12 text-center img">
                                <img src={ProfilePic} alt='my_pic'/>
                        </div>
                        <div className="col-10"> 
                        <h1 className="text-light name">
                                <strong>Hey! </strong>I'M        
                                 <Typed strings={["SOURAV"]} className="type" typeSpeed={60}/>
                            </h1>
                            <h2 className=" paragraph text-light">I am a 
                             <Typed strings={
                                 ["Webdeveloper", "Full Stack Developer","MERN Stack Developer"]
                             } typeSpeed={60} backSpeed={40} backDelay={800} loop smartBackspace/>
                            </h2>
                        </div>
                </div>

            </div>
        </HomeWrapper>
    )
}

export default Home;

const HomeWrapper= Styled.div`
    width:100%
    min-height:100vh;
    background-image:linear-gradient(rgba(0, 0, 0,0.5),rgba(0, 0, 0,0.8)),url(${BackgroundImage});
    background-size:100% 100%;
    background-repeat:no-repeat;
    .particle{
        position:absolute;
        opacity:0.8;
        
    }
    .brand{
        padding-top:80px;
        font-size:3.3rem;
        font-weight:800;
        letter-spacing:0.4rem;
        font-family:roboto;
        font-family: 'Anton', sans-serif;
font-family: 'Roboto', sans-serif;
font-family: 'Roboto Slab', serif;
    }
    .btn-1,
    .btn-2{
        display:inline-block;
        color:rgba(15, 188, 249,1.0) ;
        padding:5px 10px;
        border:1px solid rgba(15, 188, 249,1.0) ;
        margin-left:10px;
        border-radius:30px;
        font-size:1rem;
        position:relative;
        z-index:2;
        top:20px;
        transition:0.5s;
        cursor:pointer;
    }
    .btn-1{
        border:1px solid #F8F9FA;
        color:#F8F9FA;
    }
    .box1 .btn-1:hover{
        border:1px solid rgba(15, 188, 249,1.0) ;
        color:rgba(15, 188, 249,1.0);
    }
    .box1 .btn-2:hover{
        border:1px solid #F8F9FA;
        color:#F8F9FA;
    }
    .box2{
        padding:20px 0px 0px 0px;
    }
    .box2 .img{
        padding-top:20px;
    }
    .box2 img{
        width:120px;
        height:120px;
    }
    h3{
        text-align:center;
        font-size:1.2rem;
        color:rgba(15, 188, 249,1.0) !important;
    }
    .name{
        text-align:center;
        font-size:2rem;
        padding:0px 5px 0px 5px;
    }
    .name strong{
        font-size:1.2rem;
        color:rgba(15, 188, 249,1.0);
    }
    .name span{
        margin-left:4px;
    }
    .paragraph span{
        margin-left:3px;
    }
    .paragraph{
        text-align:center;
        font-size:0.8rem;
        padding-bottom:10px;
    }
    // responsiveness*****************
    @media screen and (min-width:510px){
        .brand{
            font-size:4.5rem;
            letter-spacing:0.4rem;
        }
        .box2 .img{
            padding-top:40px;
        }
        .paragraph{
            padding-bottom:40px;
        }
    }
    @media screen and (min-width:436px){
        .brand{
            font-size:3.5rem;
            letter-spacing:0.4rem;
        }
        .box2 .img{
            padding-top:60px;
        }
        .box2 img{
            width:150px;
            height:150px;
        }
        .btn-1,
    .btn-2{
        padding:7px 13px;
        margin-left:10px;
        font-size:1.3rem;
        position:relative;
        top:20px;
    }
    .name{
        font-size:2.5rem;
        padding:5px 5px 0px 5px;
    }
    .paragraph{
        font-size:1.5rem;
        padding-bottom:40px;
    }
    }
    @media screen and (min-width:768px){
        .brand{
            font-size:5rem;
            letter-spacing:0.8rem;
        }
        .box2 .img{
            padding-top:20px;
        }
        .box2 img{
            width:200px;
            height:200px;
        }
        .paragraph{
            font-size:1.5rem;
            padding-bottom:40px;
        }
    }
`